import React from 'react';
import './app.scss';
import arrow from './arrow.svg';
import { CSSTransition } from 'react-transition-group';


const list = [
  { dayName: 'Tuesday',   icon: '10', max: '20', min: '8' },
  { dayName: 'Wednesday', icon: '10', max: '20', min: '8' },
  { dayName: 'Thursday',  icon: '10', max: '20', min: '8' },
  { dayName: 'Friday',    icon: '10', max: '20', min: '8' },
  { dayName: 'Saturday',  icon: '10', max: '20', min: '8' },
  { dayName: 'Sunday',    icon: '10', max: '20', min: '8' },
  { dayName: 'Monday',    icon: '10', max: '20', min: '8' },
];

class Item extends React.Component {
  state = {
    full: false,
  }

  handleClick = () => {
    this.setState({full: !this.state.full})
    console.log('ok');
  }

  render() {
    const { full } = this.state;
    const { item } = this.props;
    return (
        <div
          className = "forecast-day"
          onClick = { this.handleClick }
        >
          <div className="forecast-day__header">
            <div className="forecast-day__column">
              <div className="forecast-day__dayName">{item.dayName}</div>
              <CSSTransition 
                in = { full }
                appear = { false }
                classNames = "arrow"
                timeout = { 300 }
              >
                <img className="forecast-day__arrow" src={arrow} alt="arrow"/>
              </CSSTransition>
            </div>
            <div className="forecast-day__column">
              <div className="forecast-day__icon"></div>
              <div className="forecast-day__max">{item.max}</div>
              <div className="forecast-day__min">{item.min}</div>
            </div>
          </div>

          <CSSTransition
            in = { full }
            appear = { false }
            timeout = { 300 }
            unmountOnExit
            classNames = "row-full"
          >
            <div
              className = "forecast__row-full"
              onClick = { this.handleClick }
            >
              <Card />
            </div>
          </CSSTransition>
       </div>
    )
  }
}

class Card extends React.Component {
  render() {
    return (
      <div className="card">
        <div className="card__weather">
          <div className="card__temp">13&deg;C</div>
          <div className="card__condition">scattered clouds</div>
          <div className="card__wind term">
            <span className="term__label">Wind: </span>
            <span className="term__value">6 m/s</span>
          </div>
          <div className="card__clouds term">
            <span className="term__label">Clouds: </span>
            <span className="term__value">6%</span>
          </div>
          <div className="card__humidity term">
            <span className="term__label">Humidity: </span>
            <span className="term__value">6%</span>
          </div>
        </div>
        <div className="card__img">
          <img src={require("./icon.svg")}></img>
        </div>
      </div>
    )
  }
}


class App extends React.Component{
  state = {
    itemFull: 'fact',
  }

  render() {
    return (
      <div className="app">
        <div className="header">
          <div className="search">Saratov, Russia</div>
          <Card />
        </div>

        <div className="forecast">
          {list.map((item) =>
            <Item item={item}/>
          )}
        </div>
      </div>
    )
  }
}

export default App;
